$red: rgb(235, 229, 209);

html {
    font-family:'Courier New', Courier, monospace;
    font-size: 16px;
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background: $red;
    * {
        // border: 1px solid rgb(205, 126, 57);
        margin: 0;
    }
}

h1 {
    font-size: 3rem;
}

img {
    display: block;
}

.container {
    display: flex;
    flex-flow: column;
    padding: 0rem 2rem;
    height: 100%;
}

.container-small {
    padding: 2rem 6rem;
}

.header {
    flex: 1;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    .brand {
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        gap: 1rem;
        .logo {
            .logo-img {
                width: 3rem;
            }
        }
        .name {
            font-size: 2rem;
            font-weight: 600;
        }
    }

    .nav {
        .nav-list {
            display: grid;
            grid-auto-flow: column;
            gap: 1.25rem;
            .nav-item {
                list-style-type: none;
                font-size: 1.25rem;
                font-weight: 500;
                .nav-link {
                    text-decoration: none;
                }
            }
        }
    }
}

.hero {
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
    border: 1px solid red;
    min-height: 400px;

    .hero-text {
        display: grid;
        gap: 1rem;
        padding-top: 2rem;

        .hero-title_number {
            font-weight: 600;
            font-size: 4rem;
            display: block;
            padding-bottom: 2rem;
        }

        .hero-title {
        }
        .hero-description {}
    }
    .hero-img {
        width: 25vw;
    }

}

.footer {
    height: 64px;
    align-items: center;
    display: grid;
    text-align: center;
    font-size: 0.8em;
}